import * as React from 'react'
import * as Raven from 'raven-js'
import * as ReactGA from 'react-ga'
import * as ReactDOM from 'react-dom'
import { IntlProvider } from 'react-intl'
import { App } from '@containers'
import registerServiceWorker from './registerServiceWorker'

const  { NODE_ENV, APP_VERSION, REACT_APP_SENTRY_KEY, REACT_APP_GOOGLE_ANALYTICS }  = process.env
const renderApp = (Component: React.ComponentType) => {
  ReactDOM.render(
    <IntlProvider locale="en">
      <Component />
    </IntlProvider>,
    document.getElementById('root') as HTMLElement
  )
}

const run = async () => {
  // Init Sentry config
  if (NODE_ENV !== 'development' && REACT_APP_SENTRY_KEY) {
    Raven.config(REACT_APP_SENTRY_KEY, {
      release: APP_VERSION,
    }).install()
  }

  if (NODE_ENV !== 'development' && REACT_APP_GOOGLE_ANALYTICS) {
    ReactGA.initialize(REACT_APP_GOOGLE_ANALYTICS)
    ReactGA.pageview(window.location.pathname + window.location.search)
  }
  await renderApp(App)
}

// tslint:disable-next-line:no-string-literal
if (module['hot']) {
  // tslint:disable-next-line:no-string-literal
  module['hot'].accept('@containers/App', () => {
    const NextApp = require('@containers/App').default
    renderApp(NextApp)
  })
}

run()
registerServiceWorker()
// tslint:disable-next-line:no-console
console.log(`Register-${APP_VERSION}`)



