import * as React from 'react'
import { Route } from 'react-router-dom'

const Home = React.lazy(() => import('@containers/Public/Home'))

const Public: React.SFC = () => {
  return(
    <React.Fragment>
      <Route exact={true} path="/" component={Home}/>
    </React.Fragment>
  )
}

export default Public



