import * as React from 'react'
import { Button } from 'antd'
import * as Raven from 'raven-js'
import ErrorImg from '@assets/error-illustration.svg'

export interface IAppErrorProps {
  children: React.ReactElement<{}>
}

export interface IAppErrorState {
  hasError: boolean
}

export default class AppError extends React.PureComponent<IAppErrorProps, IAppErrorState> {
  public static getDerivedStateFromError(error: Error) {
    return { hasError: true }
  }

  constructor(props: IAppErrorProps) {
    super(props)
    this.state = { hasError: false }
  }

  public componentDidCatch(error: Error) {
    Raven.captureException(error)
  }

  public handleGoHome = () => {
    // tslint:disable-next-line:no-console
    console.log('Go To Home')
  }

  public render() {
    if (this.state.hasError) {
      return (
        <section data-test="error-boundary">
          <img src={ErrorImg} alt="Err..." />
          <h1>Sorry, there is something wrong.</h1>
          <Button onClick={this.handleGoHome}>
            Back to home
          </Button>
        </section>
      )
    }
    return this.props.children
  }
}



