import * as React from 'react'
import { Spin } from 'antd'
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import AppError from '@containers/App/AppError'
import styles from './App.less'

const Public = React.lazy(() => import('@containers/Public'))
const Protected = React.lazy(() => import('@containers/Protected'))

const App: React.SFC = () => {
  return (
    <AppError>
      <React.Suspense fallback={<div className={styles.spinWrapper}><Spin size="large" /></div>}>
        <BrowserRouter>
          <Switch>
            <Route path="/" component={Public} />
            <Route path="/hire-freelance" component={Protected} />
          </Switch>
        </BrowserRouter>
      </React.Suspense>
    </AppError>
  )
}

export default App



